.ant-table-thead .ant-table-cell {
  background: #2d3c51 !important;
  color: #fff !important;
  padding-top: 8px;
  padding-bottom: 8px;
}

.ant-table-wrapper
  .ant-table-thead
  > tr
  > th:not(:last-child):not(.ant-table-selection-column):not(
    .ant-table-row-expand-icon-cell
  ):not([colspan])::before,
.ant-table-wrapper
  .ant-table-thead
  > tr
  > td:not(:last-child):not(.ant-table-selection-column):not(
    .ant-table-row-expand-icon-cell
  ):not([colspan])::before {
  display: none !important;
}

.ant-table-wrapper .ant-table-tbody > tr > th,
.ant-table-wrapper .ant-table-tbody > tr > td {
  padding: 5px 12px !important;
  background: #2d3c51 !important;
  color: #91abc9 !important;
  font-weight: normal !important;
  font-size: 14px !important;
}

.ant-table-wrapper .ant-table-thead > tr > th,
.ant-table-wrapper .ant-table-thead > tr > td {
  border-bottom: 1px solid #1d2633 !important;
}

.ant-table-wrapper .ant-table-tbody > tr > th,
.ant-table-wrapper .ant-table-tbody > tr > td {
  border-bottom: 1px solid #1d2633 !important;
}

.ant-table-wrapper table {
  max-width: 100%;
}

.ant-table-wrapper .ant-table-pagination-right {
  justify-content: center !important;
}

.ant-pagination .ant-pagination-prev .ant-pagination-item-link,
.ant-pagination .ant-pagination-next .ant-pagination-item-link {
  background: #21283a !important;
}

.ant-pagination .ant-pagination-prev .ant-pagination-item-link svg,
.ant-pagination .ant-pagination-next .ant-pagination-item-link svg {
  fill: #fff !important;
  /* width: 24px !important; */
}

.ant-pagination .ant-pagination-item {
  background: #21283a !important;
  font-weight: 700 !important;
  border: 0 !important;
}

.ant-pagination .ant-pagination-item a {
  color: #fff !important;
  font-size: 16px;
}

.ant-pagination .ant-pagination-item-active a {
  background: #0098EA;
  border-radius: 6px;
  height: 100% !important;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ant-pagination .ant-pagination-prev .ant-pagination-item-link,
.ant-pagination .ant-pagination-next .ant-pagination-item-link {
  font-size: 16px !important;
}

.ant-pagination .ant-pagination-item,
.ant-pagination .ant-pagination-prev,
.ant-pagination .ant-pagination-next,
.ant-pagination .ant-pagination-jump-prev,
.ant-pagination .ant-pagination-jump-next {
  height: 35px !important;
  width: 35px !important;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ant-pagination .ant-pagination-prev,
.ant-pagination .ant-pagination-jump-prev,
.ant-pagination .ant-pagination-jump-next,
.ant-pagination .ant-pagination-item {
  margin-inline-end: 15px !important;
}

.steps-launchpad-detail .ant-steps-item-finish .ant-steps-item-title,
.steps-launchpad-detail .ant-steps-item-process .ant-steps-item-title {
  color: #eaee18 !important;
  font-weight: 500 !important;
}

.steps-launchpad-detail .ant-steps-item-finish .ant-steps-item-description,
.steps-launchpad-detail .ant-steps-item-process .ant-steps-item-description {
  color: #cac4cf !important;
}

.steps-launchpad-detail .ant-steps-item-wait .ant-steps-item-description {
  color: #cac4cf !important;
  opacity: 0.4 !important;
}

.steps-launchpad-detail .ant-steps-item-wait .ant-steps-item-title {
  font-weight: 500 !important;
  color: #fff !important;
  opacity: 0.4 !important;
}

.ant-steps
  .ant-steps-item-finish
  .ant-steps-item-icon
  > .ant-steps-icon
  .ant-steps-icon-dot,
.ant-steps
  .ant-steps-item-process
  .ant-steps-item-icon
  > .ant-steps-icon
  .ant-steps-icon-dot {
  background: #50defd !important;
}

.ant-steps.ant-steps-dot .ant-steps-item-icon,
.ant-steps.ant-steps-dot.ant-steps-small .ant-steps-item-icon {
  width: 10px !important;
  height: 10px !important;
}

.ant-steps
  .ant-steps-item-finish
  > .ant-steps-item-container
  > .ant-steps-item-tail::after {
  background: #50defd;
}
.ant-steps
  .ant-steps-item-process
  > .ant-steps-item-container
  > .ant-steps-item-tail::after {
  background: linear-gradient(
    270deg,
    #50defd 0%,
    rgba(80, 222, 253, 0) 100%
