.menu-launchpad-list .ant-menu-title-content {
  display: flex;
  align-items: center;
  gap: 8px;
}

.ant-menu-dark.menu-launchpad-list,
.ant-menu-dark > .ant-menu.menu-launchpad-list {
  background: transparent !important;
}

.menu-launchpad-list .ant-menu-item {
  padding: 0 15px !important;
  width: fit-content;
  background: #1d2633 !important;
  color: white !important;
  font-weight: 600 !important;
}

.menu-launchpad-list .ant-menu-item-selected {
  /* background: linear-gradient(270deg, #ff614b 0%, #ff4a85 100%) !important; */
  background-color: #0098EA !important;
}
