.ant-form-item{
  margin-bottom: 15px;
}

.ant-form-item
  .ant-form-item-label
  > label.ant-form-item-required:not(
    .ant-form-item-required-mark-optional
  )::before {
  display: none;
}

.ant-form-item .ant-form-item-label > label {
  color: #91abc9 !important;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 12px;
}

.ant-form-vertical .ant-form-item .ant-form-item-control {
  max-height: 45.14px;
  /* background: #262b3a !important;
  border-color: #332b48 !important; */
  color: #fff;
  font-size: 14px;
}

.ant-input-outlined,
.ant-select-selector,
.ant-picker-outlined {
  background: #10161f !important;
  border-color: #10161f !important;
  color: #fff;
  padding: 8px 12px;
  font-size: 14px;
}

.ant-picker-outlined svg {
  fill: #0098EA;
}

.ant-picker .ant-picker-input > input {
  font-size: 14px !important;
}

.ant-picker-now {
  display: none !important;
}

.ant-picker-ok button {
  background: #0098EA !important;
  color: #fff !important;
  border: 0;
  padding: 5px 15px;
  height: 100%;
}

.avatar-upload {
  height: 45.14px;
  display: block;
}

.avatar-upload.ant-upload-wrapper .ant-upload-select {
  width: 100%;
  height: 100%;
}

.avatar-upload.ant-upload-wrapper .ant-upload-select .ant-upload,
.avatar-upload .ant-btn > span {
  display: flex;
  align-items: center;
  /* justify-content: center; */
  color: #556172;
  /* padding: 0 10px; */
  width: 100%;
  height: 100%;
}

.avatar-upload .ant-btn > span:hover {
  color: #556172;
}

.ant-switch {
  background: #fff !important;
}

.ant-upload-icon svg {
  fill: #556172;
}

.avatar-upload.ant-upload-wrapper
  .ant-upload-list
  .ant-upload-list-item
  .ant-upload-list-item-name {
  color: #4096ff !important;
  padding: 0 22px 0 5px;
}

.avatar-upload.ant-upload-wrapper
  .ant-upload-list
  .ant-upload-list-item-error
  .ant-upload-list-item-name {
  color: #0098EA !important;
}

.anticon-delete svg {
  fill: #0098EA !important;
}

.ant-switch .ant-switch-handle::before {
  background: gray !important;
}

.ant-switch.ant-switch-checked:not(.ant-switch-disabled) {
  background: #4096ff !important;
}

.ant-switch.ant-switch-checked .ant-switch-handle::before {
  background: #fff !important;
}

.wallet-list {
  height: 200px;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.wallet-list::-webkit-scrollbar {
  display: none;
}

.ant-modal {
  top: 50% !important;
  transform: translateY(-50%) !important;
}

.ant-modal .ant-modal-close-x svg {
  fill: #0098EA;
}

.modal-create-token {
  /* background-image: #0098EA; */
  padding: 1px;
  border-radius: 16px;
}

.ant-modal-content {
  border-radius: inherit !important;
  background-size: cover;
  background-color: #1d2633 !important;
}

