@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  font-family: "Poppins", sans-serif !important;
}

input:focus {
  outline: none !important;
}

input::-ms-input-placeholder {
  /* Edge 12-18 */
  color: #556172 !important;
}

input::placeholder,
textarea::placeholder {
  color: #556172 !important;
}

.ant-input-outlined:focus,
.ant-input-outlined:focus-within {
  border: 0 !important;
  box-shadow: none !important;
  /* background: #262b3a !important; */
}
.ant-select-selector :focus,
.ant-select-selector :focus-within {
  border: 0 !important;
  box-shadow: none !important;
  /* background: #262b3a !important; */
}

.ant-input-outlined,
.ant-select-selector {
  outline: none !important;
  box-shadow: none !important;
  border: 0 !important;
  border-radius: 10px !important;
}

a {
  cursor: pointer;
}

img {
  width: 100%;
}

body {
  margin: 0 !important;
  color: #fff;
  background: #050518;
}

/* Trending */

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

.slider {
  /* background: white; */
  /* box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.125); */
  height: 50px;
  margin: auto;
  overflow: hidden;
  position: relative;
  width: 80%;
  /* padding-left: 100px; */
}

.slider:hover .slide-track {
  animation-play-state: paused;
}

.slide-track {
  animation: scroll 25s linear infinite;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  /* min-width: 150%; */
  position: absolute;
  height: 100%;
  gap: 20px;
}

.ant-form-item-margin-offset {
  margin-bottom: 0 !important;
}

.slide {
  height: 100%;
  transition: 300ms all linear;
}

.connect-btn-condition .wallet-adapter-dropdown {
  width: 100%;
}

.connect-btn-condition .wallet-adapter-button.wallet-adapter-button-trigger {
  background: linear-gradient(90deg, #8c0bf7, #50defd) !important;
  justify-content: center;
  text-align: center;
  width: 100%;
}

.ant-layout {
  background: #10161f;
  /* background-image: url("./assets/img/bg.png"); */
  background-size: cover;
  background-attachment: fixed;
  background-position: bottom;
}

.ant-space-item {
  display: flex;
}

.ant-layout .ant-layout-sider,
.ant-menu-dark.menu-main,
.ant-menu-dark.menu-main > .ant-menu {
  background: #1d2633;
}

.ant-menu.menu-main .ant-menu-item {
  font-weight: 600;
  font-size: 15px;
}

.ant-menu-dark.menu-launchpad-list,
.ant-menu-dark.menu-launchpad-list
  > .ant-menu
  .ant-menu.menu-main
  .ant-menu-item:first-child {
  /* padding-left: 16px !important; */
  /* margin-top: 20px; */
  /* margin-bottom: 20px; */
  height: 90px !important;
  display: flex;
  align-items: center;
  margin: 0 !important;
}

.ant-layout-header {
  height: 90px;
  display: flex;
  align-items: center;
}

.ant-menu-inline-collapsed-tooltip {
  display: none;
}

.ant-btn {
  padding: 0;
  margin-left: -20px;
}

.ant-layout .ant-layout-header,
.ant-layout.ant-layout-footer {
  background: #10161f !important;
}

.ant-menu.menu-main .ant-menu-item:first-child.ant-menu-item-selected {
  background: none !important;
}

.ant-menu.menu-main .ant-menu-item {
  display: flex;
  align-items: center;
}

.ant-menu.menu-main .ant-menu-item:nth-child(2) {
  /* margin-top: 20px; */
}

.ant-menu-inline.menu-main .ant-menu-item,
.ant-menu-vertical.menu-main .ant-menu-item,
.ant-menu-inline.menu-main .ant-menu-submenu-title,
.ant-menu-vertical.menu-main .ant-menu-submenu-title {
  margin-inline: 0;
  width: 100%;
  border-radius: 0;
}

.ant-menu-dark.menu-main .ant-menu-item-selected,
.ant-menu-dark .menu-main > .ant-menu-item-selected {
  background: #141414;
  border-right: 3px solid #0098EA;
}

.menu-main .ant-menu-item-selected .ant-menu-title-content {
  background: #0098EA;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.ant-menu-inline .ant-menu-item {
  height: 50px;
  line-height: 50px;
}

.wallet-adapter-button-trigger {
  background: #0098EA !important;
  border-radius: 12px !important;
  height: 50px !important;
  line-height: 50px;
}

a:hover {
  color: inherit;
}

.bg-primary-gradient {
  background: #0098EA;
}
.bg-secondary-gradient {
  background: #fd5065;
}

.bg-home-banner {
  background-image: url("./assets/img/home-banner.png");
  background-size: cover;
  background-position: bottom;
}

.text-primary-gradient {
  background: #0098EA;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.text-secondary-gradient {
  background: #0098EA;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.bg-box-analytics {
  /* background-image: linear-gradient(to right bottom, #6a5af9, #f62682); */
  /* padding: 1px; */
  /* border-radius: 20px; */
  background: #10161f;
}

.bg-box-analytics-inner {
  border-radius: inherit;
  height: 100%;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.ant-picker.ant-picker-outlined {
  border: 0 !important;
}

.ant-select-single.ant-select-open .ant-select-selection-item {
  color: #fff !important;
}

.ant-select-selection-item {
  color: #fff !important;
}

.ant-select .ant-select-arrow {
  color: #ff614b !important;
}

@media screen and (max-width: 768px) {
  .wallet-adapter-button-trigger {
    border-radius: 10px !important;
    height: 40px !important;
    line-height: 40px;
    padding: 0 15px !important;
    font-size: 14px !important;
  }
  .slider {
    width: 100%;
  }
  .slide-track {
    gap: 10px;
  }
}
